/* Search functionality styles */
.adex-search-bar {
    display: flex;
    padding: 8px 10px;
    background-color: #f5f5f5;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 10px;
}

.adex-search-input-container {
    display: flex;
    flex: 1;
    max-width: 400px;
    position: relative;
}

.adex-search-input {
    flex: 1;
    padding: 6px 12px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    font-size: 14px;
    outline: none;
}

.adex-search-input:focus {
    border-color: #147fd4;
    box-shadow: 0 0 0 2px rgba(20, 127, 212, 0.2);
}

.adex-search-button {
    background-color: #147fd4;
    color: white;
    border: none;
    border-radius: 0 4px 4px 0;
    padding: 6px 12px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -1px;
}

.adex-search-button:hover {
    background-color: #0e6cb4;
}

.adex-search-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.adex-search-loading {
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: white;
    animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.adex-search-results {
    display: flex;
    align-items: center;
    margin-left: 16px;
}

.adex-search-count {
    font-size: 14px;
    color: #555;
    margin-right: 16px;
}

.adex-search-navigation {
    display: flex;
    gap: 8px;
}

.adex-search-prev,
.adex-search-next {
    background-color: #f0f0f0;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 4px 8px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.adex-search-prev:hover,
.adex-search-next:hover {
    background-color: #e0e0e0;
}

.adex-search-prev:disabled,
.adex-search-next:disabled {
    background-color: #f5f5f5;
    color: #aaa;
    cursor: not-allowed;
}

.adex-search-sidebar-toggle {
    background-color: transparent;
    border: none;
    color: #555;
    cursor: pointer;
    padding: 4px;
    margin-left: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
}

.adex-search-sidebar-toggle:hover {
    background-color: #e0e0e0;
}

.adex-search-close {
    background-color: transparent;
    border: none;
    color: #555;
    cursor: pointer;
    padding: 4px;
    margin-left: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
}

.adex-search-close:hover {
    background-color: #e0e0e0;
}

/* Search highlight styles - Update these */
.adex-search-highlight {
    position: absolute;
    background-color: rgba(255, 255, 0, 0.4);
    border-radius: 2px;
    z-index: 10;
    pointer-events: none;
}

.adex-search-highlight.current {
    background-color: rgba(255, 165, 0, 0.6);
    box-shadow: 0 0 0 2px rgba(255, 165, 0, 0.8);
    z-index: 11;
}

/* Mobile search styles */
.adex-mobile .adex-search-bar {
    flex-direction: column;
    align-items: stretch;
}

.adex-mobile .adex-search-input-container {
    max-width: 100%;
}

.adex-mobile .adex-search-results {
    margin-left: 0;
